import React from "react"
import { Layout } from "components"
import { Row, Col, List, Avatar, Typography, Form, Icon } from "antd"
import { useQuery, useMutation } from "react-apollo"
import { QUERY_PUBLIC_ASSIGNMENTS, MUTATE_FILTER_ASSIGNMENTS } from "queries/profile"
import { newClient, pricer, mobile, serverBaseUrl } from "helpers"
import { useState } from "react"
import { Card, Empty, Section, Heading } from "components"
import moment from "moment"
import { Formik } from "formik"
import TextInput from "components/forms/TextInput"
import { useHistory } from "react-router"
import { country } from "App"
import FadeIn from "react-fade-in"
import { Trans } from "@lingui/macro"
import { countryMap } from "helpers/countryMapping"
import DynamicIcon from "components/common/DynamicIcon"
import Button from "components/Button"
import styled from "styled-components"
import AdSection from "components/AdSection"

const client = newClient("posting_assignment")
const { Paragraph, Text } = Typography
const initialValues = { keywords: "", location: "", subject: 0, curriculum: 0 }

const ButtonMore = styled.div`
	position: absolute;
	bottom: -10px;
	left: 50%;
	transform: translate(-50%);
	width: 80%;
`

export let publicAssignmentsForSitemap = null

export default function Public() {
	const [assignments, setAssignments] = useState([])
	const [isExpanded, setIsExpanded] = useState(false)
	const { push } = useHistory()
	const symbol = countryMap[country].symbol
	const accessToken = localStorage.getItem("token")

	const { loading } = useQuery(QUERY_PUBLIC_ASSIGNMENTS, {
		client,
		variables: { country },
		onCompleted: (data) => {
			data = data.listPostingAssignmentPublic || []
			publicAssignmentsForSitemap = data
			setAssignments(data)
		},
	})

	const [filterAssignments, { loading: loadingFilter }] = useMutation(MUTATE_FILTER_ASSIGNMENTS, {
		client,
		variables: { country },
		onCompleted: (data) => {
			data = data.findAssignment
			setAssignments(data)
		},
	})

	const isLoading = loading || loadingFilter

	const handleFilter = (values, { setSubmitting }) => {
		filterAssignments({ variables: { ...values, keywords: values.keywords } }).finally(() => setSubmitting(false))
	}

	const handleClickAssignment = (title, id) => {
		title = /\?/.test(title) ? title.replace(/\?/, "") : title
		push({ pathname: `/${country}/assignments/${title}--${id}`, state: { country } })
	}

	const handleClickNewAssignment = () => {
		return accessToken
			? push(`/${country}/assignments/post_assignment`)
			: push({ pathname: `/${country}/login`, state: { previousRoute: `/assignments/post_assignment` } })
	}

	return (
		<Layout>
			<Section className={mobile ? "pt2em" : "pt4em"}>
				<Row type="flex" justify="space-between" className="mb1em">
					<Col lg={12} xs={24}>
						<Row>
							<Col lg={3} className={mobile && "mb1em"}>
								<DynamicIcon type="icon-task" size={40} />
							</Col>
							<Col lg={20}>
								<Heading
									content={<Trans>Find and Post Tuition Assignments</Trans>}
									subheader={
										<Trans>
											Advertise for FREE here and relevant tutors will automatically be notified and apply directly.
										</Trans>
									}
									level={4}
									marginBottom="2em"
								/>
								<AdSection
									title={<Trans>Want to be the first to receive every new assignments?</Trans>}
									description={
										<Trans>
											Join our{" "}
											<a href="https://t.me/cudyassignments" target="_blank" rel="noopener noreferrer">
												Telegram <DynamicIcon type="icon-telegram" /> channel
											</a>{" "}
											today
										</Trans>
									}
								/>
							</Col>
						</Row>
					</Col>
					<Col lg={12} xs={24} className="ta-right">
						<Formik initialValues={initialValues} onSubmit={handleFilter}>
							{({ handleSubmit }) => (
								<Form onSubmit={handleSubmit}>
									<Row
										gutter={16}
										type="flex"
										justify={mobile ? "start" : "end"}
										className={mobile ? "ta-left" : "ta-right"}
									>
										<Col lg={10} xs={12}>
											<TextInput name="keywords" placeholder="E.g. Mathematics..." allowClear />
										</Col>
									</Row>
								</Form>
							)}
						</Formik>
					</Col>
				</Row>
				<Row type="flex" justify="end" className="mb2em">
					<Col lg={6} xs={24}>
						<Button block type="primary" icon="plus" onClick={handleClickNewAssignment}>
							&nbsp;<Trans>New assignment</Trans>
						</Button>
					</Col>
				</Row>
				<List
					itemLayout="horizontal"
					grid={{ gutter: mobile ? 16 : 32, lg: 4, xs: 2 }}
					dataSource={assignments}
					loading={isLoading}
					locale={{ emptyText: <Empty description={<Trans>No assignments</Trans>} /> }}
					renderItem={(item) => (
						<FadeIn>
							<List.Item className="mb2em">
								<Card height={mobile ? "420px" : "380px"} className="pos-rel">
									<Row gutter={16} className="mb1em">
										<Col lg={6}>
											<Avatar src={serverBaseUrl + item.resource.path} />
										</Col>
										<Col lg={18}>
											<Paragraph strong className="mb0" ellipsis={{ rows: 2 }}>
												{item.title}
											</Paragraph>
											<Paragraph type="secondary" className="fs-09em mb1em">
												{item.programmeSubjectName}, {item.programmeLevelName}
											</Paragraph>
											<Paragraph type="secondary" className="fs-09em mb0" ellipsis>
												<Icon type="clock-circle" />
												&nbsp; {moment(item.createdAt).fromNow()}
											</Paragraph>
										</Col>
									</Row>

									<Row>
										<Col lg={24}>
											<Text type="secondary" className="fs-09em">
												Location
											</Text>
											<Paragraph ellipsis={{ rows: 3 }}>{item.location}</Paragraph>
										</Col>
									</Row>

									<Row type="flex" justify="space-between">
										<Col lg={12} xs={12}>
											<Text type="secondary" className="fs-09em mb0">
												Gender
											</Text>
											<Paragraph>{item.tutorGender}</Paragraph>
										</Col>
										<Col lg={12} xs={12}>
											<Text type="secondary" className="fs-09em mb0">
												Price
											</Text>
											<Paragraph>
												{symbol}
												{pricer(item.priceRangeStartPrice)} - {symbol}
												{pricer(item.priceRangeEndPrice)}
											</Paragraph>
										</Col>
									</Row>

									<ButtonMore>
										<Button type="primary" block onClick={() => handleClickAssignment(item.title, item.pvid)}>
											&nbsp;<Trans>See details</Trans>&nbsp; <Icon type="right" />
										</Button>
									</ButtonMore>
								</Card>
							</List.Item>
						</FadeIn>
					)}
				/>
			</Section>
		</Layout>
	)
}
